@import url('https://fonts.googleapis.com/css?family=Work+Sans:400,700,900');

* {
	font-family: 'Work Sans', sans-serif;
}

//COLORS
$grey: #7F7F7F;
$grey-blue: #142A38;
$accent: #00B8BA;
$red: #F22613;
$green: #20bf6b;

// GENERAL

div.block {
	background: white;
	padding:20px;
	@extend .z-depth-1;
	width:100%;
	transition: opacity 0.1s ease-out;
	height:auto;
	margin-top: 30px;
	margin-bottom:30px;
	h4 {
		margin-bottom:20px;
		position: relative;
		padding-bottom:10px;
		display: inline-block;
	}
	h4:before {
		content: "";
	    position: absolute;
	    width: 30%;
	    height: 5px;
	    bottom: 0;
	    left: 0;
	    border-bottom: 3px solid $accent;
	}
}

table.table {
	margin-bottom:0;
	th {
		border-top: none;
	}
}

.hide {
	opacity: 0;
	height:0!important;
	overflow: hidden;
	padding:0!important;
	margin:0!important;
}

.alerts-holder {
	.alert {
		border-radius: 0px;
		border: none;
	}
	position:fixed;
	top:80px;
	right:30px;
	z-index: 2000;
}

.alert-success, .alert-error{
	background-color: $green;
	color: white;
	.span {
		color: white;
		&:hover {
			color: $grey;
		}
	}
}

.alert-error {
	background-color: $red;
}

.dropdown-menu {
	@extend .z-depth-1-half;
	border-radius: 0px;
	border: none;
	.dropdown-item {
		cursor: pointer!important;
	}
	.dropdown-item:active {
		background: black;
	}
	.dropdown-item.delete:active {
		background: $red;
		color: white;
	}
}

.modal-content{
	border-radius: 0px;
	border: none;
	box-shadow: none;
}

.modal-body {
	padding-top: 0;
	padding-bottom:25px;
}
.modal-backdrop.in {
    opacity: 0.9;
}

.modal-header {
	border-bottom: none;
}

.sortable-handle {
	color: grey;
	transition: 0.1s ease;
	cursor: ns-resize;
	&:hover {
		color: black;
	}
	&:active {
		color: black;
	}
}

// Forms
input.form-control, textarea.form-control {
	border-radius: 0px!important;
	-webkit-appearance: none;
	&:focus {
		outline: none;
		box-shadow: none;
		border-color: black;
	}
}

select.form-control {
	-webkit-appearance: none;
    -moz-appearance: none;
    background-position: right 50%;
    background-repeat: no-repeat;
    border-radius: 0;
    background-image: url(data:image/svg+xml,%3C%3Fxml%20version%3D%221.0%22%20encoding%3D%22utf-8%22%3F%3E%20%3C%21DOCTYPE%20svg%20PUBLIC%20%22-//W3C//DTD%20SVG%201.1//EN%22%20%22http%3A//www.w3.org/Graphics/SVG/1.1/DTD/svg11.dtd%22%3E%20%3Csvg%20version%3D%221.1%22%20id%3D%22Layer_1%22%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20xmlns%3Axlink%3D%22http%3A//www.w3.org/1999/xlink%22%20x%3D%220px%22%20y%3D%220px%22%20width%3D%2214px%22%20height%3D%2212px%22%20viewBox%3D%220%200%2014%2012%22%20enable-background%3D%22new%200%200%2014%2012%22%20xml%3Aspace%3D%22preserve%22%3E%20%3Cpolygon%20points%3D%223.862%2C7.931%200%2C4.069%207.725%2C4.069%20%22/%3E%3C/svg%3E);
    padding-right: 1.5em;
    &:focus {
		outline: none!important;
		box-shadow: none!important;
		border-color: black;
	}
}

.form-check-input {
	margin-top:.25rem;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    margin: 0; 
}

form .has-error {
	border-color: red;
}
// Typography
a {
	color: $accent;
	&:hover {
		text-decoration: none;
	}
}

h1 {
	font-weight:900;
	letter-spacing:2px;
	text-transform: uppercase;
}

h5 a {
	color:#7F7F7F;
	&:hover {
		color:$accent;
		text-decoration: none;
	}
}

.note, .caption {
	color: $grey;
	font-size: 0.8em;

}

.caption {
	letter-spacing:1px;
	margin-bottom:0.5rem;
	margin-top:2rem;
	text-transform: uppercase;
}

.modal-title {
	@extend .caption;
	margin-top:0px;
	font-size:1.1rem;
}

.text-bold {
	font-weight: 700;
}

.text-red {
	color: $red;
}

.text-green {
	color: $green;
}

// Buttons
a.button, input.button, button, .modal button[type="submit"]{
	padding: 8px 16px;
	background: $accent;
	color:white;
	font-weight:400;
	font-size:1em;
	letter-spacing:1px;
	transition:  0.2s ease-in;
	outline: none;
	border-style: none;
	&:hover {
		color:white;
		text-decoration: none;
		@extend .z-depth-1-half;
	}
}

button:focus, a:focus {
	outline: none;
}

button.close:hover {
	box-shadow: none;
}

a.button.secondary, button.button.secondary {
	background: $grey;
}

a.close.delete-variant span{
	transition-duration: 0.1s;
	&:hover {
		color: $red;
	}
}

a.close.delete-variant.highlight {
	color: $red;
}

.button.delete {
	background-color: $red!important;
	color:white!important;
	cursor: pointer;
}

button.button.ghost {
	box-shadow: none;
	background: white;
	border: 1px solid $grey;
	color: $grey;
	&.active {
		color: $accent;
		border-color: $accent;
	}
	&:hover {
		box-shadow: none;
		cursor: pointer;
		color: $accent;
		border-color: $accent;
	}
}

div.option-dropdown {
	width:30px;
	height:30px;
	font-size: 1.5em;
	transition: 0.1s ease;
	text-align: center;
	color: $grey;
	&:hover {
		cursor: pointer;
		color: black;
	}
}

.dropright.active .option-dropdown {
	color: black;
}

td {
	.option-dropdown {
		font-size: 1em;
		height: 18px;	
	}
	.dropdown-menu {
		top: -12px!important;
	}
}

tr.active {
	background: #ECECEC;
}

// Z-levels
.z-depth-0 {
  box-shadow: none !important;
}
.z-depth-1{
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
}
.z-depth-1-half{
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
}
.z-depth-2{
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.z-depth-3{
  box-shadow: 0 12px 15px 0 rgba(0, 0, 0, 0.24), 0 17px 50px 0 rgba(0, 0, 0, 0.19);
}
.z-depth-4{
  box-shadow: 0 16px 28px 0 rgba(0, 0, 0, 0.22), 0 25px 55px 0 rgba(0, 0, 0, 0.21);
}
.z-depth-5{
  box-shadow: 0 27px 24px 0 rgba(0, 0, 0, 0.2), 0 40px 77px 0 rgba(0, 0, 0, 0.22);
}

.hoverable:hover {
  transition: box-shadow .25s;
  box-shadow: 0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}




//Admin
.admin{
	background: #F8F9FA;
	.navbar {
		// box-shadow: none;
		border-bottom: 0px;
		background: white!important;
		z-index:4;
		margin-left:80px;
		padding-left:30px;
		.dropdown-menu {
			right:0!important;
			left: auto;
			border-radius: 0px;
			border: 0px;
		}
	}

	.sidebar {
		width:80px;
		padding-top:0px;
		background-color: $grey-blue;
		height:100vh;
		position:fixed;
		top:0;
		z-index:1;
		a p {
			font-size:0.8em;
			color:#7F7F7F;
			&:hover {
				text-decoration: none;
				color:black;
			}
		}
		a:hover {
			text-decoration: none;
		}
		.item:not(.logo) {
			width:100%;
			height:80px;
			padding-top:10px;
			svg{
				width:40px;
				fill: #7F7F7F;
			}
			&:hover {
				cursor: pointer;
				svg {
					fill: white!important;
				}
				a p {
					text-decoration: none;
					color: white;
				}
			}
		}
		.logo {
			padding:2px;
		}

		.active {
			p {
				font-weight: 700;
				color:white;
			}
			svg {
				fill: white!important;
			}
		}
	}

	.content {
		padding-left:80px;
		width:100%;
		padding-top: 56px;
		padding-bottom:100px;

		.page-header {
			position: relative;
			background: white;
			padding-left:30px;
			z-index:3;
			padding-bottom:20px;
		}
	}
}